
.generic-button[data-v-0f20c7a0] {
  -webkit-appearance:none;
  -moz-appearance:none;
  border:none;
	display: inline-block;
	box-sizing: border-box;
	cursor:pointer;
	padding: 0.5rem 5rem 0.4rem 2em;
	background-color: white;
	background-repeat: no-repeat;
	background-image: url('../../assets/buttons/generic-button-bg.png');
	background-position: top right;
	background-size: auto 312%;
	border-bottom: 0.3rem solid #ff940d;
}
.generic-button[data-v-0f20c7a0]:hover {
	background-position: 100% 100%;
	border-bottom-color: #ffb619;
}
.generic-button[data-v-0f20c7a0]:active {
	background-position: 100% 50%;
	border-bottom-color: #334754;
}



