#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height:100vh;
}
.topbar {
  background-color: white;
  padding: 0.5rem;
  display: -ms-flexbox;
  display: flex;
  max-width: 100%;
}
.logo-banner {
  background: linear-gradient(to right, #ff7200, #ffb71a);
}
@media (max-height: 564px){
.logo-banner {
    display: none;
}
}
.logo-banner p {
  color:white;
  margin:0;
  text-transform: uppercase;
  padding-left: 12rem;
  line-height: 3.5vh;
}
.logo-banner a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}
.logo img{
  margin-bottom: -5%;
  margin-left: 5rem;
  max-height: 12vh;
  min-height: 3rem;
}
.login-area{
  max-height: 12vh;
  min-height: 2rem;
}
.page {
  max-width: 1000px;
  width: calc(100% - 4rem);
  margin: 0 auto 2rem;
}
main {
  position: relative;
  -ms-flex-positive: 1;
      flex-grow: 1;
}



